* {
   font-family: 'Roboto', sans-serif; }

nav {
   height: 50px;
   background-color: #212121; }

.menu {
   display: flex;
   justify-content: space-between;
   padding: 13px 70px;
   margin: 0;
   list-style: none; }

.menu_link {
   color: #FFF;
   font-size: 15px;
   font-weight: 300;
   text-decoration: none; }

.menu_item {
   border-right: 2px dotted black;
   padding-right: 28px; }

.subheader {
   position: absolute;
   width: 100%;
   height: 84px;
   background-color: rgba(0, 0, 0, 0.6); }

.subheader_logo {
   display: block;
   margin-top: 16px;
   max-width: 100%; }

.subheader_official {
   margin-top: 6px;
   opacity: 0.5;
   color: #FFF;
   font-size: 13px;
   font-weight: 100; }

.subheader_call {
   margin-top: 19px;
   color: #FFF;
   text-align: right;
   font-size: 12px;
   font-weight: 300; }

.subheader_phone {
   display: block;
   text-decoration: none;
   color: #FFF;
   text-align: right;
   font-family: Roboto;
   font-size: 22px;
   font-weight: 400;
   text-transform: uppercase;
   line-height: 22px; }

.subheader_btn {
   margin-top: 19px;
   width: 172px;
   height: 46px;
   box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
   border: none;
   border-radius: 4px;
   background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
   color: #ffffff;
   font-size: 14px;
   font-weight: 400;
   text-transform: uppercase; }

.promo {
  min-height: 800px;
  background: url(../img/uber_bg.jpg) center (center / cover) no-repeat;
  padding: 150px 0 135px 0; }

.promo_header {
   color: #FFF;
   font-size: 60px;
   font-weight: 700;
   text-transform: uppercase;
   line-height: 60px;
   margin: 0;
   text-align: center; }

.promo_subheader {
   color: #FFF;
   font-size: 36px;
   font-weight: 700;
   line-height: 36px;
   text-transform: uppercase;
   margin: 0;
   margin-top: 22px;
   text-align: center; }

.promo_descr {
   color: #FFF;
   text-align: center;
   font-size: 18px;
   font-weight: 400;
   line-height: 24px;

   /* 133.333% */
   margin-top: 23px; }

.promo_btn {
   display: block;
   margin: 118px auto 0 auto;
   width: 259px;
   height: 63px;
   border: none;
   border-radius: 4px;
   background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
   color: #ffffff;
   font-size: 14px;
   font-weight: 400;
   text-transform: uppercase; }

.advantages {
   min-height: 850px;
   padding: 20px 0 64px 0; }

.advantages_quality {
   display: flex;
   justify-content: center;
   align-items: center;
   margin: 0 auto;
   width: 113px;
   height: 24px;
   background-color: black;
   color: #FFF;
   font-size: 11px;
   font-weight: 400;
   line-height: 11px;

   /* 100% */
   text-transform: uppercase; }

.advantages {
   .advantages_title {
      margin-top: 33px;
      color: #222;
      text-align: center;
      font-family: Roboto;
      font-size: 38px;
      font-weight: 700;
      line-height: 48px;

      /* 126.316% */
      text-transform: uppercase; } }

.advantages_cards {
   margin-top: 16px; }

.advantages_image {
   display: flex;
   justify-content: center;
   align-items: center;
   width: 116px;
   height: 116px;
   border-radius: 100%;
   background-color: #1EACC7; }

.advantages_card {
   margin-top: 27px;
   display: flex;
   justify-content: center;
   align-items: center;

   &:nth-child(2n) {
      align-items: flex-start; } }

.advantages_descr {
   max-width: 371px;
   max-height: 150px;
   margin-left: 25px;
   color: #202020;
   font-size: 15px;
   font-weight: 300;
   line-height: 21px;

   span {
      color: #1EACC7;
      font-family: Roboto;
      font-size: 17px;
      font-style: normal;
      font-weight: 400; } }

.label {
   display: flex;
   justify-content: center;
   align-items: center;
   margin: 0 auto;
   width: 113px;
   height: 24px;
   background-color: black;
   color: #FFF;
   font-size: 11px;
   font-weight: 400;
   line-height: 11px;
   text-transform: uppercase; }

.title {
   margin-top: 33px;
   color: #222;
   text-align: center;
   font-family: Roboto;
   font-size: 38px;
   font-weight: 700;
   line-height: 48px;

   /* 126.316% */
   text-transform: uppercase; }

.subtitle {
   color: #222;
   text-align: center;
   font-family: Roboto;
   font-size: 17px;
   font-style: normal;
   font-weight: 400; }

.mobile {
   min-height: 864px;
   padding: 48px 0 79px 0;
   background: url('../img/mobile_bg.jpg') center (center/cover) no-repeat;
   .row {
      margin-top: 32px; }
   &_item {
      width: 320px;
      height: 280px;
      margin: 0 auto;
      margin-bottom: 19px;
      padding-top: 92px;
      &_1 {
         background: url('../icons/card1.png') center (center/cover) no-repeat; }
      &_2 {
         background: url('../icons/card2.png') center (center/cover) no-repeat; }
      &_3 {
         background: url('../icons/card3.png') center (center/cover) no-repeat; }
      &_4 {
         background: url('../icons/card4.png') center (center/cover) no-repeat; }
      &_5 {
         background: url('../icons/card5.png') center (center/cover) no-repeat; }
      &_6 {
         background: url('../icons/card6.png') center (center/cover) no-repeat; }
      &_subtitle {
         color: #FFF;
         text-align: center;
         font-size: 24px;
         font-weight: 400;
         text-transform: uppercase; }
      &_plus {
         display: flex;
         width: 26px;
         height: 26px;
         background-color: #000000;
         margin: 0 auto;
         border-radius: 100%; } } }

